export const buildGetRequest = url => {
    const xhr = new XMLHttpRequest();
    xhr.open("GET", url);

    xhr.send();

    return new Promise(function(resolve, reject) {
        xhr.onreadystatechange = () => {
            if (xhr.readyState === 4) {
		if (xhr.status === 200) {
                	console.log(xhr);
                	resolve(xhr.responseText);
            	} else {
                	reject(xhr.responseText);
            	}
	     }
        }
    })
};